    getGoodsUseCouponInfo() {
      console.log(this.voucherList, 'voucherList')
      let type = this.voucherList.some(item => item.marking === 'sheng') ? 'sheng' : this.voucherList.some(item => item.marking === 'cou') ? 'cou' : ''
      console.log(type, 'type');
      let yhprice = 0; let buyNum = 1;
      this.voucherList.forEach(item => {
        if (item.marking === 'sheng' || item.marking === 'cou') {
          yhprice += item.couponAmount
          buyNum = item.buyNum
        }
      })
      return {
        type,
        buyNum,
        price: (this.priceInfo.actualPrice * buyNum - yhprice) > 0 ? this.priceInfo.actualPrice * buyNum - yhprice : 0,
        yhprice,
        voucherLen: this.voucherList.length
      }
    },
