        sessionStorage.setItem('isRecordShareRelation', true)
        this.$http.get(URL.recordShareRelation, {
          params: {
            sharerMemberId: this.$store.state.shareId || '',
            storeProductId: this.$route.query.productId || '',
            storeId: this.o2oStoreId || ''
          }
        })
