            if (this.$util.isSyBird() || this.$route.query.fromSource === 'syn') {
              this.toSYNFootPrint()
            }
            this.$util.gioTrackWithVariable('ProView', {
              member_id: localStorage.getItem('mId') ? localStorage.getItem('mId') + '' : 'null',
              user_id: localStorage.getItem('ucId') ? localStorage.getItem('ucId') + '' : 'null',
              deliverStreet: this.$store.state.locationInfo.township || 'null',
              deliverDistrict: this.$store.state.locationInfo.district || 'null',
              deliverCity: this.$store.state.locationInfo.city || 'null',
              deliverProvince: this.$store.state.locationInfo.province || 'null',
              storeId: this.gioObject.storeId ? this.gioObject.storeId + '' : 'null',
              stockStatus: this.gioObject.stockStatus ? this.gioObject.stockStatus + '' : 'null',
              productCode: this.gioObject.productEncode ? this.gioObject.productEncode + '' : 'null',
              productName_var: this.gioObject.productName || 'null',
              productId_var: this.gioObject.productId ? this.gioObject.productId + '' : 'null',
            })
            if (this.$util.isHaierApp()) {
              // 获取分享信息
              this.productQrCode()
            }
            // 二次分享
            this.$store.dispatch('WeChatSecondShare', {
              title: this.products.productFullName,
              desc: this.products.productTitle,
              // thumImage: `${this.swiperImgs[0].video ? this.swiperImgs[1] : this.swiperImgs[0]}`,
              thumImage: this.getShareImage(),
            })
            this.setStorageToShare()
          })
        })
      })
      enterTime = Date.now()
    },

    /**
     * @description 切换 spu后刷新整个页面所有数据
    */
    fnSelectSpu(obj) {
      // 如果有视频实例 进行销毁 防止回退时无法实例化
      if (this.$refs.swiper.player) {
        this.$refs.swiper.player.dispose()
        this.$refs.swiper.player = null
        this.$refs.swiper.closeSmallVideo()
        this.$refs.swiper.isCancelSmallVideo = false
      }

      // const { query: _q } = this.$route
      // if (_q.exclusiveId) {
      //   this.queryExclusivePrice(_q.exclusiveId) // 查询专属价使用信息
      // }
      if (utils.isHaierApp()) {
        this.doShowUplusLive()
      }
      this.productId = obj.productId
      // this.visitStore = this.$route.query.visitStore
      // this.storeId = this.$route.query.storeId || '' // 分享人id
      // this.show_live = this.$route.query.show_live || ''
      this.hasProgram()
      this.relation()
      this.promos()
      this.specifications()
      this.evaluation()
      this.getCommentsByCondition()
      if (this.$util.isIhaierApp()) {
        // ihaier 显示分享按钮
        this.showShareFlag = true
      } else if (this.$util.isHaierApp()) {
      // U+ 显示分享按钮
        this.showShareFlag = true
        // U+ 登陆处理
        // const authFlag = await this.$store.dispatch('handleUpAuth')
        if (this.$store.getters.getLoginStatus) {
          this.getCartNumber()
        }
      } else {
        if (this.$store.getters.getLoginStatus) {
          this.getCartNumber()
        }
      }
      this.getProductBasicInfo().then(res1 => {
        this.productNumber = 1 // 切换spu时重置购买数量
        this.dynamic().then(res2 => {
          this.fetchIoucCost()
          this.$util.gioTrackWithVariable('ProView', {
            member_id: localStorage.getItem('mId') ? localStorage.getItem('mId') + '' : 'null',
            user_id: localStorage.getItem('ucId') ? localStorage.getItem('ucId') + '' : 'null',
            deliverStreet: this.$store.state.locationInfo.township || 'null',
            deliverDistrict: this.$store.state.locationInfo.district || 'null',
            deliverCity: this.$store.state.locationInfo.city || 'null',
            deliverProvince: this.$store.state.locationInfo.province || 'null',
            storeId: this.gioObject.storeId ? this.gioObject.storeId + '' : 'null',
            stockStatus: this.gioObject.stockStatus ? this.gioObject.stockStatus + '' : 'null',
            productCode: this.gioObject.productEncode ? this.gioObject.productEncode + '' : 'null',
            productName_var: this.gioObject.productName || 'null',
            productId_var: this.gioObject.productId ? this.gioObject.productId + '' : 'null',
          })
          if (this.$util.isHaierApp()) {
          // 获取分享信息
            this.productQrCode()
          }
          // 二次分享
          this.$store.dispatch('WeChatSecondShare', {
            title: this.products.productFullName,
            desc: this.products.productTitle,
            // thumImage: `${this.swiperImgs[0].video ? this.swiperImgs[1] : this.swiperImgs[0]}`,
            thumImage: this.getShareImage(),
          })
          // spu切换 底部按钮刷新逻辑
          this.refreshSpuBtn()
          // 同步spu选中信息
          this.attrValueNames = obj.skuShortName;
          this.attrLen = (this.attrValueNames.length === 0 ? '' : this.attrValueNames.split(',').length);
        })
      })
    },
    // 禁用标题长按选择
    notSelectTitle() {
      this.isCanSelectTitle = false
    },
    /**
     * @description 调起切换地址弹窗
     */
    changeAddress() {
      this.$refs.goodsInfo.openAddr()
    },
    hiddenFeedBack() {
      this.showTotop = false
    },
    /*
			 * @description spu商品弹窗内切换 ，按钮刷新
    */
    refreshSpuBtn() {
      // openFrom: 1, // 从何处打开的选择弹窗 1：已选 2：真实按钮
      // lastSelectRealBtn 0: 购物车
      if (this.openFrom === 1) {
        // 如果是点已选过来的，切换spu后按点已选逻辑刷新弹窗按钮
        this.refreshBtnType()
      } else {
        if (this.activityInfo && this.activityInfo.activityType === 81) {
          // 活动类型为立即预约的 弹窗按钮类型同步
          this.btnType = 8
        } else if (this.sgStockInfo.bookable) {
          // 点击真实按钮过来的，如果切换后商品为预定商品展示预定按钮 或定金尾款活动按钮
          if (this.activityInfo && this.activityInfo.activityType === 100) {
            if (this.activityInfo.deposit === 1) {
              if (this.lastSelectRealBtn === 4) {
                this.btnType = 4 // 原价购买
              } else {
                this.btnType = 5 // 支付定金
              }
            } else {
              this.btnType = 5 // 支付定金
            }
          } else {
            this.btnType = 3
          }
        } else {
          // 如果非预定商品 4 种情况
          // 0：购物车
          // 1：立即购买
          // 4：原价购买
          // 5：定金支付
          // 如果购物车按扭过来的 并且 允许展示购物车
          if (this.lastSelectRealBtn === 0 && this.baseInfo.supportCart) {
            this.btnType = 0
          } else {
            if (this.activityInfo && this.activityInfo.activityType === 100) {
              if (this.activityInfo.deposit === 1) {
                if (this.lastSelectRealBtn === 4) {
                  this.btnType = 4 // 原价购买
                } else {
                  this.btnType = 5 // 支付定金
                }
              } else {
                this.btnType = 5 // 支付定金
              }
            } else if (this.activityInfo && this.activityInfo.activityType === 75) { // 拼团
              if (this.lastSelectRealBtn === 9) {
                this.btnType = 9
              } else {
                this.btnType = 10
              }
            } else {
              this.btnType = 1
            }
          }
        }
      }
    },
    /**
     * @description 单品页基本静态信息_new
     */
    attrSelectChanged() {

    },
    // 启用标题长按选择
    selectTitle() {
      this.isCanSelectTitle = true
