    /**
     * 模拟动画滚动
    */
    scrollSmoothTo(position) {
      console.log('#scrollSmoothToscroll')
      if (!window.requestAnimationFrame) {
        window.requestAnimationFrame = function(callback) {
          return setTimeout(callback, 17);
        };
      }
      // debugger
      let _target = document.querySelector('.inner-view')
      // 当前滚动高度
      let scrollTop = _target.scrollTop;
      console.log(scrollTop, 'scrollTop')
      // 滚动step方法
      let step = function () {
        // 距离目标滚动距离
        var distance = position - scrollTop;
        // 目标滚动位置
        scrollTop = scrollTop + distance / 10;
        if (Math.abs(distance) < 1) {
          _target.scrollTo(0, position);
        } else {
          _target.scrollTo(0, scrollTop);
          requestAnimationFrame(step);
        }
      };
      step();
    },
    handleScroll(e) {
      if (this.cancelScroll) {
        return
      }
      this.$util.throttle(() => {
        this.navShowFlag = this.$refs.scrollBox.scrollTop > this.RootEM * 0.92
        let _gcTop = Math.abs(this.$refs.goodsContainer.getBoundingClientRect().top)
        let _dcTop = this.showComment ? Math.abs(this.$refs.detailContainer.getBoundingClientRect().top) : 0;
        let _ccTop = this.showComment ? Math.abs(this.$refs.commentContainer.getBoundingClientRect().top) : 0;
        this.scrollTop = _gcTop;
        // console.log('_gcTop:' + _gcTop + ',_dcTop:' + _dcTop + ',_ccTop:' + _ccTop + ',RootEM' + this.RootEM)
        // if (_dcTop >= 0 && _dcTop <= this.RootEM * 0.92) this.activeAnchor = 'detailContainer'
        // if (_ccTop >= 0 && _ccTop <= this.RootEM * 0.92) this.activeAnchor = 'commentContainer'
        // if (_gcTop >= 0 && _gcTop <= this.RootEM * 0.92) this.activeAnchor = 'goodsContainer'

        if (!this.showComment) {
          let scrollHeight = e.target.scrollHeight;// div里内容的高度，根据内容的增加和减少变化
          let scrollTop = e.target.scrollTop;// 滚动条的最上端到，div上端内里框框的高度
          let clientHeight = e.target.clientHeight;// div内里框框的高度，是死的
          if ((scrollHeight - clientHeight === scrollTop)) {
            this.showComment = true;
          }
        }
        if (this.$refs.scrollBox.scrollTop > 200) {
          this.showTotop = true
        } else {
          this.showTotop = false
        }
        this.$nextTick(() => {
          if (_gcTop < _dcTop && _gcTop < _ccTop) {
            this.activeAnchor = 'goodsContainer'
          } else if (_dcTop < _gcTop && _dcTop < _ccTop) {
            this.activeAnchor = 'detailContainer'
          } else if (_ccTop < _dcTop && _ccTop < _gcTop) {
            this.activeAnchor = 'commentContainer'
            if (!this.showComment) {
            }
          }
        })
      }, 200)()
    },
