    /**
     * @description 处理静态接口中的多规格数据
     */
    calcAttr() {
      let res = this.shopInfo
      let _checkedAttrIds = []
      let _attrValueNames = []
      let _checkedAttrKeys = []
      let _checkedAttr = {}
      if (res.data.checkedAttr) {
        this.checkedAttr = res.data.checkedAttr
        _checkedAttrIds = res.data.checkedAttr.split(',')
        _checkedAttrKeys = Object.keys(res.data.sgAttribute)
        _checkedAttrKeys.forEach((item, index) => {
          _checkedAttr[item] = _checkedAttrIds[index]
        })
      }
      // 针对多规格与单规格特殊处理，单规格默认选中，多规格默认不选中 初始化显示规格标题拼接
      // 活动多规格默认选中 默认显示选中规格
      if (this.hasSubProduct && !this.checkedAttr) {
        // 多规格处理
        for (let key in res.data.sgAttribute) {
          _attrValueNames.push(res.data.sgAttribute[key][0].attrName)
        }
      } else {
        // 单规格处理
        for (let key in res.data.sgAttribute) {
          res.data.sgAttribute[key].forEach((item, index) => {
            if (item.sgAttribute.id === _checkedAttr[key] * 1) {
              item.selected = true
              _attrValueNames.push(item.sgAttribute.attrValueName)
            } else {
              item.selected = false
            }
          })
        }
      }
      if (!this.spuInfo) {
        this.attrValueNames = _attrValueNames.join(',')
      }
      this.holdSelected = _attrValueNames.join(',');

      if (this.hasSubProduct) {
        // 多规格情况 因为默认不选中， 选中规格长度手动置空
        // 活动多规格默认选中 规格长度按默认配置计算
        this.attrLen = this.checkedAttr ? this.attrValueNames.split(',').length : 0 // 初始化的时候算一下长度
      } else {
        // 单规格走老逻辑
        this.attrLen = this.attrValueNames.split(',').length // 初始化的时候算一下长度
      }

      this.sgAttribute = res.data.sgAttribute
      this.sgStoreAttribute = res.data.sgStoreAttribute
      this.originAttrs = res.data.sgStoreAttribute ? res.data.sgStoreAttribute.filter((v, i) => v.num >= 0) : []
      if (res.data.sgStoreAttribute) {
        res.data.sgStoreAttribute.forEach(item => {
          if (this.checkedAttr === item.attrValueIds) {
            this.sgStoreAttributeItem = item
            this.skku = item.skku
            // this.productNumber = item.num ? 1 : 0
          }
        })
      } else {
        this.sgStoreAttributeItem = null
      }
    },
    /**
     * @description 单品页动态信息接口_new
     */
    dynamic() {
      const { query: _q } = this.$route
      return new Promise((resolve, reject) => {
        dynamic({
          prodId: this.productId,
          mdCode: sessionStorage.getItem('shareMdCode') || '',
          // channel: '',
          memberStoreId: this.getShareId || '',
          number: this.productNumber || 1, // 不能传0
          // number: 1, // 不能传0
          provinceId: this.location.provinceId,
          cityId: this.location.cityId,
          regionId: this.location.districtId,
          streetId: this.location.townshipId,
          latitude: this.location.latitude,
          longitude: this.location.longitude,
          skku: this.skku || null,
          sku: this.products.sku,
          visitStore: _q.exclusiveId ? this.getShareId : this.visitStore,
          exclusivePriceId: _q.exclusiveId || null,
          directSaleType: _q.directSaleType || null,
          sceneCode: _q.sceneCode || null,
          sceneVersion: _q.sceneVersion || null,
          // 分佣逻辑参数
          comId: _q.comId,
        }).then(res => {
          if (res.success) {
            this.activityInfo = res.data.activityInfo || null // 活动信息，为空时没有参与活动，非必返回，有返回值时替换原有活动内容
            this.baseInfo = {
              ...res.data.baseInfo,
            } // 基础信息，必定返回
            this.priceInfo = res.data.priceInfo || null // 价格信息对象，必定返回
            // console.log(this.priceInfo, 'aaaaaaaaaaaaaaaaaaa')

            this.noticeContent = res.data.noticeInfo // 优先使用paltformNoticeContent
              ? res.data.noticeInfo.platformNoticeContent
                ? res.data.noticeInfo.platformNoticeContent
                : res.data.noticeInfo.storeNoticeContent
                  ? res.data.noticeInfo.storeNoticeContent
                  : undefined
              : undefined
            this.sgStockInfo = res.data.sgStockInfo ? { ...res.data.sgStockInfo } : null // 库存信息，必定返回
            // if (this.sgStockInfo) { this.sgStockInfo._hideStockNum = this.hideStockNum }
            if (this.sgStockInfo && (this.sgStockInfo.skuDisplayState === 3 || this.sgStockInfo.skuDisplayState === 4 || this.sgStockInfo.skuDisplayReason)) {
              // 不在配送范围/区域不可销售 不可销售状态置为true
              this.notSaleStatus = true
            } else {
              this.notSaleStatus = false
            }
            this.storeInfo = res.data.storeInfo || null // 店铺信息，不一定返回
            if (this.storeInfo) {
              // storeInfo存在的话获取商品所属店铺id
              this.staticStoreId = res.data.storeInfo.o2oStoreId
            } else {
              this.staticStoreId = ''
            }
            // 埋点库存状态
            let stockStatus = ''
            if (this.sgStockInfo.hasStock) {
              stockStatus = '有货'
            } else {
              stockStatus = '无货'
            }
            // 店铺id
            if (res.data.storeInfo === null) {
              this.o2oStoreId = ''
            } else {
              this.o2oStoreId = res.data.storeInfo.o2oStoreId
            }
            this.$set(this.gioObject, 'stockStatus', stockStatus)
            this.$set(this.gioObject, 'storeId', this.o2oStoreId)
            if (this.baseInfo.image) {
              if (this.swiperImgs && this.swiperImgs[0] && this.swiperImgs[0].baseImage) {
                this.swiperImgs[0].baseImage = this.baseInfo.image
              } else {
                this.swiperImgs = [{ baseImage: this.baseInfo.image || '' }, ...this.shopInfo.data.swiperImgs]
              }
              if (this.products.videoUrl) {
                let videoUrl = this.$util.strJSONParse(this.products.videoUrl, { video: this.products.videoUrl })
                this.swiperImgs = [{ baseImage: this.swiperImgs[0].baseImage }, {
                  img: videoUrl.img,
                  video: videoUrl.video,
                }, ...this.shopInfo.data.swiperImgs]
              }
            } else {
              if (this.products.videoUrl) {
                let videoUrl = this.$util.strJSONParse(this.products.videoUrl, { video: this.products.videoUrl })
                this.swiperImgs = [{
                  img: videoUrl.img,
                  video: videoUrl.video,
                }, ...this.shopInfo.data.swiperImgs]
              } else {
                this.swiperImgs = this.shopInfo.data.swiperImgs
              }
            }
            // 初始化商品选中数量
            // if (!(this.sgAttribute && this.sgStoreAttribute)) {
            //   this.productNumber = (this.sgStockInfo.stockNum || this.sgStockInfo.stockNum === 0) ? 1 : 0
            // }

            // TODO 处理多规格逻辑
            // 如果是单规格预定商品 模拟 500库存
            // if (this.sgStockInfo.bookable && this.sgStockInfo.stockNum === 0 && !this.hasSubProduct) {
            //   // this.sgStockInfo.stockNum = 500
            // }
            // 1.如果是无子商品多规格，获取动态接口的价格与库存，有子商品不做处理
            if (!this.hasSubProduct) {
              this.shopInfo.data.sgStoreAttribute[0].num = this.sgStockInfo.stockNum
              if (this.activityInfo && this.activityInfo.activityType === 100) {
                this.shopInfo.data.sgStoreAttribute[0].price = this.priceInfo.originPrice
              } else {
                this.shopInfo.data.sgStoreAttribute[0].price = this.priceInfo.actualPrice
              }
            }
            if (!this.attrInitStatus) {
              // // 1.如果是无子商品多规格，获取动态接口的价格与库存，有子商品不做处理
              // if (!this.hasSubProduct) {
              //   this.shopInfo.data.sgStoreAttribute[0].num = this.sgStockInfo.stockNum
              //   if (this.activityInfo && this.activityInfo.activityType === 100) {
              //     this.shopInfo.data.sgStoreAttribute[0].price = this.priceInfo.originPrice
              //   } else {
              //     this.shopInfo.data.sgStoreAttribute[0].price = this.priceInfo.actualPrice
              //   }
              // } else {
              //   // 验证 主商品 是否马上预定的 多规格商品
              //   // 多规格商品 进入页面时 主商品可预订 则所有子商品可预订
              //   if (this.sgStockInfo.bookable) {
              //     this.parentBookable = true
              //   }
              // }
              // 2.处理多规格数据
              this.calcAttr()
              this.fetchIoucCost()
              this.attrInitStatus = true
              // 3.处理spu数据
              // this.spuInfo = res.data.virtualSpuInfo
              if (this.spuInfo) {
                this.spuInfo.spuSpecsList[0].spuSkuList.forEach(i => {
                  if (i.productId === Number(this.productId)) {
                    this.attrValueNames = i.skuShortName;
                  }
                })
              }
            }

            // 复购分佣
            if (res.data.storeInfo) {
              this.fetchRecordShareRelation()
            }

            // 设置优惠券信息
            this.getProductAvailableCoupons()
            this.querySingleOnlineRightsList()
            // 加载完成
            this.isSkeletonLoading = true;
            Promise.all([lazyGoodsDetail(), lazyCommentInfo(), lazyShopInfo()]).then(() => {
              this.showComment = true
              console.log('====================================');
              console.log('加载完成');
              console.log('====================================');
            })

            resolve()
          } else {
            this.$toast(res.message)
          }
        })
      })
    },
