    receiveToast(status, time) {
      // console.log(time)
      const icon = status ? require('@/assets/imgs/cart/icon-receive-success.png') : require('@/assets/imgs/cart/icon-receive-error.png')
      const message = status ? '领取成功' : '领取失败'
      this.$toast({ message, icon, duration: 1500 })
      if (time) {
        this.$nextTick(() => {
          const wrapper = document.querySelector('.van-toast')
          if (wrapper.querySelectorAll('.toast-add-text').length) {
            return
          }
          const text = document.createElement('span')
          text.className = 'toast-add-text'
          text.innerHTML = `${time}可用`
          wrapper.appendChild(text)
          const removeTimer = window.setTimeout(() => {
            wrapper.removeChild(text)
            window.clearTimeout(removeTimer)
          }, 1550)
        })
      }
    },
