    fnSelectAttribute(item, stepperNum) {
      this.$refs.swiper.player && this.$refs.swiper.player.dispose()
      if (this.$refs.swiper.player) this.$refs.swiper.smallVideoShow = false
      this.sgStoreAttributeItem = item
      this.skku = item.skku
