    /**
     * @description 评论展开收起
     */
    fnShowCommentContent(data) {
      if (data.type === 'commentContent') {
        // 评论
        this.commentList[data.index].longContent = !this.commentList[data.index].longContent
      } else if (data.type === 'experienceContent') {
        // 追评
        this.commentList[data.index].longExperience = !this.commentList[data.index].longExperience
      } else if (data.type === 'serviceReplyContent') {
        // 客服回复
        this.commentList[data.index].serviceCommentReplies[data.subIndex].longService = !this.commentList[data.index].serviceCommentReplies[data.subIndex].longService
      } else if (data.type === 'replyContent') {
        // 商家回复
        this.commentList[data.index].storeCommentReplies[data.subIndex].long = !this.commentList[data.index].storeCommentReplies[data.subIndex].long
      }
