      if (!this.baseInfo.supportCart) {
        // 不允许加入购物车
        this.$toast('此商品暂时不能加入购物车')
        return
      }
      // await this.checkStockForNum(otype) // 校验库存
      const { comId, caseId, directSaleType } = this.$route.query
      let _goodsInfo = {
        attrValueNames: this.hasSubProduct ? this.attrValueNames || null : null, // 只有含子商品多规格才传规格名称
        number: this.productNumber,
