              user_id: this.ucId ? this.ucId + '' : 'null',
            })
            this.addCart(_goodsInfo)
          })
      } else {
        gio('track', 'add_to_cart', {
          user_id: this.ucId,
          member_id: this.mId,
          productId_var: this.productId,
        })
        console.log('加入购物车的商品信息：', _goodsInfo)
