    /**
     * @description 加购物车、购买检查库存
     */
    checkStockForNum(otype) {
      let _param = this.getCheckStockParams() // 获取校验库存参数
      return new Promise((resolve, reject) => {
        checkStockForNum(_param).then(res => {
          if (res.success) {
            if ((!res.data || !res.data.hasStock) && otype !== '5') {
              // otype为5时是 马上预定
              this.$toast('库存不足')
            } else {
              resolve()
            }
          } else {
            this.$toast(res.message)
          }
        })
      })
    },
    getCheckStockParams: function() {
      const stockType = this.sgStockInfo.stockType
      const skku = this.skku
      const prodId = this.productId
      const regionId = this.location.districtId
      const o2oStoreId = this.storeInfo ? this.storeInfo.o2oStoreId : null
      const storeCode = this.storeInfo ? this.storeInfo.storeCode : null
      const streetId = this.location.townshipId
      const memberId = this.$store.getters.mId
      const number = this.productNumber
      let params
      if (stockType === 'WA') {
        params = {
          stockType: stockType,
          productId: prodId,
          number: number,
          memberId: memberId,
          streetId: streetId,
          regionId: regionId,
        }
      } else if (stockType === 'O2O') {
        params = {
          stockType: stockType,
          productId: prodId,
          number: number,
          memberId: memberId,
          streetId: streetId,
          o2oStoreId: o2oStoreId,
          storeCode: storeCode,
        }
      } else {
        params = {
          stockType: stockType,
          productId: prodId,
          number: number,
          memberId: memberId,
          streetId: streetId,
        }
      }
      if (skku) {
        params.sku = skku
      } else {
        params.sku = null
      }
      return params
    },
