      return new Promise((resolve, reject) => {
        let queryParam = {
          ac: ac || '1', // 不参与定金尾款或拼团：1，参与定金尾款或拼团：0
          ch: this.$util.isHaierApp() ? 61 : 60, // 渠道号：安卓55，IOS50，H560，微信：70，支付宝：75，智家：80，PC：99, h5:60, 61：智家app
          proList: proList,
          shareId: this.getShareId || '',
          street: this.location.townshipId,
          isOrderRight: '1', // 是否需要权益 '1': 需要; '0': 不需要
          v: 2,
        }
        if (this.baseInfo.showCoupons) {
          queryParam.coupon = this.voucherList.length && this.voucherList.map(item => {
            if (item.marking === 'sheng' && item.displayType === '1') {
              return {
                activityCode: item.activityCode,
                couponTemplateCode: item.couponTemplateCode
              }
            }
          })
        }
        createOrder(queryParam).then(res => {
          resolve(res)
        })
      })
    },
    /**
     * @description 商品收藏,取消收藏
     */
    fnCollect() {
      if (this.islogining) return;
      // 未登录处理
      if (!this.$store.getters.getLoginStatus) {
        this.islogining = true
        this.$store.dispatch('doLogin').then(res => {
          if (res !== 400) {
            this.islogining = false
            this.reload()
          } else {
            this.islogining = false
          }
        }).catch(() => {
          this.islogining = false
        })
        return
      }
      if (this.isCollected === 0) {
        this.productCollection()
      } else if (this.isCollected === 1) {
        this.cancelProductCollection()
      }
    },

    /**
     * @description 单品页收藏接口
     */
    productCollection() {
      // if (!this.verifyTagCheckStatus()) {
      //   if (!this.$refs.goodsInfo.showInfoFlag) {
      //     this.$refs.goodsInfo.showInfoFlag = true
      //     return
      //   } else {
      //     return this.$toast('规格选择不完整')
      //   }
      // }
      const getSwiperImg = () => {
        const imgs = this.swiperImgs.length > 1 ? this.swiperImgs[1] : ''
        if (typeof imgs === 'object') {
          return imgs['img']
        } else {
          return imgs
        }
      }
      const { query } = this.$route
      const o2oStoreId = this.storeInfo ? this.storeInfo.o2oStoreId : undefined
      productCollection({
        PCGName: this.location.region, // 省市区街道名称
        imageUrl: typeof this.swiperImgs[0] === 'object' ? getSwiperImg() : this.swiperImgs[0], // 图片地址
        // PCGName: this.location.region, // 省市区街道名称
        // imageUrl: typeof this.swiperImgs[0] === 'object' ? this.swiperImgs[1] : this.swiperImgs[0], // 图片地址
        productId: this.productId, // 商品ID
        productName: this.products.productName, // 商品名称
        sku: this.products.sku, // 主物料编码
        shareId: this.getShareId ? this.getShareId : undefined,
        visitStore: this.visitStore,
        comId: query.comId,
        undertakeStore: o2oStoreId,
        directSaleType: query.directSaleType,
      }).then(res => {
        if (res.success) {
          if (res.data) {
            this.isCollected = 1
            this.$toast('收藏成功')
            // 智家app收藏埋点
            this.$util.gioTrackWithVariable('MB10802', {
              user_id: this.ucId ? this.ucId + '' : 'null',
              member_id: this.mId ? this.mId + '' : 'null',
              productId_var: this.productId + '',
              productName_var: this.products.productName || 'null'
            })
            // 新M埋点
            // gio('track', 'MB10802', {
            //   user_id: this.ucId,
            //   member_id: this.mId,
            //   productId_var: this.productId,
            // })
          } else {
            this.isCollected = 0
          }
        } else {
          this.$toast(res.message)
        }
      })
    },
    /**
     * @description 单品页取消收藏
     */
    cancelProductCollection() {
      cancelProductCollection({
        sku: this.products.sku,
        // productId: this.productId, // 商品ID
      }).then(res => {
        if (res.success) {
          if (res.data) {
            this.isCollected = 0
            this.$toast('取消收藏成功')
            this.$util.gioTrackWithVariable('MB13408', {
              // user_id: localStorage.getItem('ucId') ? localStorage.getItem('ucId') + '' : 'null',
              // member_id: localStorage.getItem('mId') ? localStorage.getItem('mId') + '' : 'null',
              productId_var: this.productId + '',
              // productName_var: this.products.productName || 'null'
            })
          } else {
            this.isCollected = 1
          }
        } else {
          this.$toast(res.message)
        }
      })
    },
    /**
     * @description 显示输入手机号通知页面
     * localStorage.getItem('notifycationMobile')，获取暂存本地的手机号
     */
    fnArrivalNotice() {
      // 不可销售时禁用购买
      if (this.notSaleStatus) return
      this.dialogShow = !this.dialogShow
      this.mobile = localStorage.getItem('notifycationMobile') || ''
      if (this.dialogShow) {
        this.$util.gioTrackWithVariable('MB13410', {
          user_id: localStorage.getItem('ucId') ? localStorage.getItem('ucId') + '' : 'null',
          member_id: localStorage.getItem('mId') ? localStorage.getItem('mId') + '' : 'null',
          productId_var: this.productId + ''
        })
      }
    },
    /**
     * @description 已选点击回调
     * groupId： 拼团参团Id
     */
    toggleShowInfo(groupId) {
      this.chooseGroupId = groupId;
      if (!(this.activityInfo && this.activityInfo.activityType === 81)) {
        console.log(this.activityInfo)
        if (this.sgStockInfo && !this.sgStockInfo.hasStock) {
          // 无库存
          if (this.activityInfo && this.activityInfo.activityType === 100 && this.sgStockInfo.bookable) {
            if (this.activityInfo.deposit !== 1) {
              this.openAttrFromToggle(5)
            } else {
              this.openAttrFromToggle(7)
            }
          } else if (this.sgStockInfo.bookable || this.parentBookable) {
            // 预定商品
            this.openAttrFromToggle(3)
          } else if (!this.sgStockInfo.bookable) {
            if (!this.notSaleStatus) {
              // 到货通知
              this.openAttrFromToggle(2)
            } else {
              // 有库存
              if (this.activityInfo && this.activityInfo.activityType === 100) {
                if (this.activityInfo.deposit !== 1) {
                  this.openAttrFromToggle(5)
                } else {
                  this.openAttrFromToggle(7)
                }
              } else {
                // 立即购买
                // if (this.baseInfo.supportCart) {
                this.openAttrFromToggle(6)
                // } else {
                // this.openAttrFromToggle(1)
                // }
              }
            }
          }
        } else {
          // 有库存
          if (this.activityInfo && this.activityInfo.activityType === 100) {
            if (this.activityInfo.deposit !== 1) {
              this.openAttrFromToggle(5)
            } else {
              this.openAttrFromToggle(7)
            }
          } else if (this.activityInfo && this.activityInfo.activityType === 75) { // 店铺拼团
            if (groupId) {
              this.openFrom = 2;
              this.openAttrFromToggle(10)
            } else {
              this.openAttrFromToggle(11)
            }
          } else {
            // 立即购买
            if (this.baseInfo.supportCart) {
              this.openAttrFromToggle(6)
            } else {
              this.openAttrFromToggle(1)
            }
          }
        }
      } else {
        this.openAttrFromToggle(8)
      }
    },
    /**
     * @description 多规格内切换规格时刷新底部按钮
     */
    refreshBtnType() {
      if (!(this.activityInfo && this.activityInfo.activityType === 81)) {
        if (this.sgStockInfo && !this.sgStockInfo.hasStock) {
          // 无库存
          if (this.activityInfo && this.activityInfo.activityType === 100 && this.sgStockInfo.bookable) {
            if (this.activityInfo.deposit !== 1) {
              this.btnType = 5
            } else {
              this.btnType = 7
            }
          } else if (this.sgStockInfo.bookable || this.parentBookable) {
            // 预定商品
            this.btnType = 3
          } else if (!this.sgStockInfo.bookable) {
            if (!this.notSaleStatus) {
              // 到货通知
              this.btnType = 2
            } else {
              // 有库存
              if (this.activityInfo && this.activityInfo.activityType === 100) {
                if (this.activityInfo.deposit !== 1) {
                  this.btnType = 5
                } else {
                  this.btnType = 7
                }
              } else {
                // 立即购买
                // if (this.baseInfo.supportCart) {
                this.btnType = 6
                // debugger
                // } else {
                //   this.btnType = 1
                // }
              }
            }
          }
        } else {
          // 有库存
          if (this.activityInfo && this.activityInfo.activityType === 100) {
            if (this.activityInfo.deposit !== 1) {
              this.btnType = 5
            } else {
              this.btnType = 7
            }
          } else if (this.activityInfo && this.activityInfo.activityType === 75) { // 拼团
            if (this.openFrom === 1) {
              this.btnType = 11
            } else {
              if (this.lastSelectRealBtn === 9) {
                this.btnType = 9
              } else {
                this.btnType = 10
              }
            }
          } else {
            // 立即购买
            if (this.baseInfo.supportCart) {
              this.btnType = 6
            } else {
              this.btnType = 1
            }
          }
        }
      } else {
        this.btnType = 8
      }
    },
    async fnNotice() {
      // 校验手机号
      if (!/^1([3456789][0-9]|4[01356789]|66|9[89])\d{8}$/.test(this.mobile)) {
        this.$toast('手机号格式不正确')
        return
      }
      // 通过手机格式校验后，暂存手机号到本地缓存
      localStorage.setItem('notifycationMobile', this.mobile)
      await this.checkOnly()
      this.insertNotice()
    },
    /**
     * @description 检验到货通知是否发送
     */
    checkOnly() {
      return new Promise((resolve, reject) => {
        checkOnly({
          productId: this.productId, // 商品ID
          mobile: this.mobile,
          provinceId: this.location.provinceId,
          cityId: this.location.cityId,
          areaId: this.location.districtId,
          streetId: this.location.townshipId,
        }).then(res => {
          if (res.success) {
            if (res.result === 0) {
              resolve()
            } else {
              this.$toast('该手机号在该地区已经申请到货通知，不能重复申请！')
            }
          } else {
            this.$toast(res.message)
          }
        })
      })
    },
    /**
     * @description 添加到货通知
     */
    insertNotice() {
      insertNotice({
        productId: this.productId, // 商品ID
        mobile: this.mobile,
        provinceId: this.location.provinceId,
        cityId: this.location.cityId,
        areaId: this.location.districtId,
        streetId: this.location.townshipId,
        sku: this.skku ? this.skku : this.products.sku,
        productName: this.products.productName,
        provinceName: this.location.province,
        cityName: this.location.city,
        areaName: this.location.district,
        o2oStoreId: this.storeInfo ? this.storeInfo.o2oStoreId : null,
      }).then(res => {
        if (res.success) {
          this.$util.gioTrackWithVariable('MB13411', {
            user_id: localStorage.getItem('ucId') ? localStorage.getItem('ucId') + '' : 'null',
            member_id: localStorage.getItem('mId') ? localStorage.getItem('mId') + '' : 'null',
            productId_var: this.productId + ''
          })
          this.$toast('您的资料已经提交，到货后我们会第一时间通知您！')
        } else {
          this.$toast(res.message)
        }
      })
    },
    fnOpenCustomerService() {
      if (this.islogining) return
      let isShowLive = this.$route.query.show_live
      if (!this.$store.getters.getLoginStatus) {
        this.islogining = true
        this.$store.dispatch('doLogin').then(res => {
          if (res !== 400) {
            this.islogining = false
            if (+isShowLive) {
              instance.initDeviceReady().then(() => {
                instance.upLiveModule.closeFloatWindow().then(result => {
                  this.toCustomerService()
                }).catch(() => {
                  this.toCustomerService()
                })
              })
            } else {
              this.toCustomerService()
            }
          } else {
            this.islogining = false
          }
        }).catch(() => {
          this.islogining = false
        })
        return
      }
      if (+isShowLive) {
        instance.initDeviceReady().then(() => {
          instance.upLiveModule.closeFloatWindow().then(result => {
            this.toCustomerService()
          }).catch(() => {
            this.toCustomerService()
          })
        })
      } else {
        this.toCustomerService()
      }
    },
    toCustomerService() {
      this.$util.gioTrackWithVariable('MB13412', {
        user_id: localStorage.getItem('ucId') ? localStorage.getItem('ucId') + '' : 'null',
        member_id: localStorage.getItem('mId') ? localStorage.getItem('mId') + '' : 'null',
        productId_var: this.productId + ''
      })
      if (this.$store.getters.getLoginStatus) {
        this.$store.dispatch('goPageByType', {
          type: 29,
          params: {
            accountId: this.storeInfo && this.storeInfo.storeCode ? this.storeInfo.storeCode : '',
            sceneId: 'cj091'
          }
        })
      }
    },
