     * @description 验证传入的88码是否开通商家客服
    */
    isCanGetCustomer(storeCode) {
      return new Promise((resolve, reject) => {
        const data = {
          storeCode: storeCode
        };
        personalApi.getCustomer(data).then(res => {
          if (res.data.success) {
            resolve(res.data.result)
          }
        })
      })
    },
    fnOpenCart() {
      if (this.islogining) return
      this.$util.gioTrackWithVariable('MB15626')
      // 未登录 处理
      if (!this.$store.getters.getLoginStatus) {
        this.islogining = true
        this.$store.dispatch('doLogin').then(res => {
          if (res !== 400) {
            this.islogining = false
            this.reload()
          } else {
            this.islogining = false
          }
        }).catch(() => {
          this.islogining = false
        })
        return
      }
      let queryParam = {
      }
      if (this.originSource) {
        queryParam.originSource = this.originSource
      }
      this.$router.push({
        path: '/cart',
        query: queryParam,
      })
