    fnScrollTo(refName, event) {
      event.preventDefault()
      this.cancelScroll = true;
      this.activeAnchor = refName
      this.showComment = true
      Promise.all([lazyGoodsDetail(), lazyCommentInfo(), lazyShopInfo()]).then(() => {
        this.$nextTick(() => {
          this.$refs[refName].scrollIntoView({
            block: 'start',
          })
        })
      })
      setTimeout(() => {
        this.cancelScroll = false;
      }, 100)
