    shareLinkFactory() {
      let url = `${location.origin}${location.pathname}`
      const query = {
        ...this.getCleanQuerys(),
        productId: this.productId,
      }

      if (this.mId) {
        query.storeId = this.mId
        query.shareId = this.mId
      }
      if (this.storeInfo && this.storeInfo.mdCode) {
        query.mdCode = this.storeInfo.mdCode
      }
      const queryString = utils.parseParamsToQuery(query)
      return url + queryString
    },
    copyLink () {
      const url = this.shareLinkFactory()
      const a = document.createElement('a')
      const clipboard = new ClipboardJS(a, {
        text: function (trigger) {
          return url
        }
      });
      clipboard.on('success', () => {
        this.$toast('复制链接成功');
      })
      clipboard.on('error', () => {
        this.$toast('复制链接失败');
      })
      a.click()
    },
