      let _param = {}
      if (this.skku) {
        _param.sku = this.skku
      }
      if (this.storeId) {
        // 分享人id
        _param.storeId = this.storeId
      }
      productQrCode({
        productId: this.productId,
        channelId: 3, // 4微商城，不传或者3为M站APP
        spu: this.products.sku,
        streetId: this.location.townshipId,
        ..._param,
      }).then(res => {
        if (res.success) {
          // 超出2行 溢出隐藏
          if (res.data.productName && res.data.productName.length > 48) {
            res.data.productName = `${res.data.productName.slice(0, 48)}...`
          }

          this.shareGoodsInfo = {
            ...res.data,
          }
        }
      })
    },
    async fnReserve() {
      // 不可销售时禁用购买
      if (this.notSaleStatus) return
      if (this.islogining) return
      // 判断登陆
      if (!this.$store.getters.getLoginStatus) {
        this.islogining = true
        this.$store.dispatch('doLogin').then(res => {
          if (res !== 400) {
            this.islogining = false
            this.reload()
          } else {
            this.islogining = false
          }
        }).catch(() => {
          this.islogining = false
        })
        return
      }
      await this.checkMemberReserve()
      this.reservationShowFlag = true
    },
    /**
     * @description 检查是否可以预约
     */
    checkMemberReserve() {
      return new Promise((resolve, reject) => {
        checkMemberReserve({
          acReserveId: this.activityInfo.activityId,
          productId: this.productId,
        }).then(res => {
          if (res.success) {
            if (res.data) {
              resolve()
            } else {
              this.$toast(res.message)
            }
          } else {
            this.$toast(res.message)
          }
        })
      })
    },
    /**
     * @description 预约抢购发送短信
     */
    sendSmsCaptcha(mobile) {
      sendSmsCaptcha({
        mobile,
      }).then(res => {
        if (res.success) {
        } else {
          this.$toast(res.message)
        }
      })
    },
    /**
     * @description 确认预约
     */
    reserve(mobile, mobileCode) {
      reserve({
        acReserveId: this.activityInfo.activityId,
        productId: this.productId,
        mobile,
        mobileCode,
        provinceId: this.location.provinceId,
        cityId: this.location.cityId,
        districtId: this.location.districtId,
        streetId: this.location.townshipId,
      }).then(res => {
        if (res.success) {
          if (res.data) {
            this.$toast('商品预约成功，可在个人中心-我的预约中查看预约商品')
          } else {
            this.$toast(res.message)
          }
        } else {
          this.$toast(res.message)
        }
      })
    },
    fnChangeReservationShowFlag() {
      this.reservationShowFlag = !this.reservationShowFlag
    },
    commentBack() {
      this.commentHeader = false
    },
    toggleCommentHeader() {
      this.commentHeader = !this.commentHeader
    },
    /**
     * swiperChange
     */
    swiperChange(index) {
      this.curSwiper = index + 1;
    },
    /**
     * sceenChange
     */
    sceenChange(flag) {
      this.isAllSceenSwiper = flag
    },
    /**
     * @description 重置按钮类型
     */
    resetBtnType() {
      // this.btnType = -1
    },
    setStorageToShare () {
      // isSyBirdStore: this.storeInfo && Number(this.storeInfo.synStoreType) !== 9,
      const params = {
        mdCode: this.storeInfo && this.storeInfo.mdCode ? this.storeInfo.mdCode : '',
        actualPrice: this.priceInfo.actualPrice,
        spuList: this.spuInfo ? this.spuInfo.spuSpecsList[0].spuSkuList.map((spu) => {
          return { productId: spu.productId, skuShortName: spu.skuShortName }
        }) : []
      }
      const mainIndex = params.spuList.findIndex((item) => {
        return item.productId === Number(this.productId)
      })
      if (mainIndex === -1) {
        params.spuList.unshift({
          productId: Number(this.productId),
          productFullName: this.products.productFullName
        })
      } else {
        const item = params.spuList.splice(mainIndex, 1)[0]
        item.productFullName = this.products.productFullName
        params.spuList.unshift(item)
      }
      const jsonStr = JSON.stringify(params)
      localStorage.setItem('shareMessage', jsonStr)
    }
  },
}
</script>
